import React from 'react';
import ReactDom from 'react-dom';

import DatePicker from 'material-ui/DatePicker';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import last from 'ramda/src/last';

import './index.css';

function formatDate(date) {
    return date.toLocaleString('en-US', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
    });
}

function formatDay(date) {
    return date.toLocaleString('en-US', {
        weekday: 'short'
    });
}

function createDatesArray(date) {
    let dates = [date];
    for (let i = 0; i < 13; i++) {
        let lastDate = last(dates);
        let newDate = new Date(lastDate.getTime() + 1000 * 60 * 60 * 24);
        dates.push(newDate);
    }
    return dates;
}

class App extends React.Component {
    state = { currentDate: new Date() };

    handleDateChange = (e, date) => {
        this.setState({ currentDate: date });
    };

    render() {
        const { currentDate } = this.state;

        const dates = createDatesArray(currentDate);

        return (
            <React.Fragment>
                <div id="input">
                    <h1>Time Sheet Generator</h1>

                    <DatePicker
                        hintText="Select Date"
                        onChange={this.handleDateChange}
                        value={currentDate}
                    />
                </div>

                <div id="container">
                    <table className="header">
                        <tbody>
                            <tr>
                                <td>
                                    <h1>City of Huntsville Police</h1>
                                    <span>
                                        Officer: _______________________________
                                    </span>
                                </td>
                                <td>
                                    <h1>Biweekly Time Sheet</h1>
                                    <table>
                                        <tr>
                                            <td>
                                                <span>
                                                    Pay period start date:
                                                </span>
                                            </td>
                                            <td className="underline">
                                                <span>
                                                    {formatDate(currentDate)}
                                                </span>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <span>
                                                    Pay period end date:
                                                </span>
                                            </td>
                                            <td className="underline">
                                                <span>
                                                    {formatDate(last(dates))}
                                                </span>
                                            </td>
                                        </tr>
                                    </table>
                                </td>
                            </tr>
                        </tbody>
                    </table>

                    <table className="data">
                        <thead>
                            <th>Day</th>
                            <th>Date</th>
                            <th>
                                Regular<br />Hours
                            </th>
                            <th>
                                Overtime<br />Hours
                            </th>
                            <th>
                                Sick<br />Hours
                            </th>
                            <th>
                                Vacation<br />Hours
                            </th>
                            <th>
                                Comp.<br />Hours
                            </th>
                        </thead>
                        <tbody>
                            {dates.map(date => (
                                <tr>
                                    <td>{formatDay(date)}.</td>
                                    <td>{formatDate(date)}</td>
                                    <td />
                                    <td />
                                    <td />
                                    <td />
                                    <td />
                                </tr>
                            ))}
                        </tbody>
                    </table>

                    <br />

                    <table className="fields">
                        <tr>
                            <td>Comp. hours due</td>
                            <td>_______</td>
                            <td>Sick hours due</td>
                            <td>_______</td>
                            <td>Vacation hours due</td>
                            <td>_______</td>
                        </tr>
                        <tr>
                            <td>Comp. hours accrued</td>
                            <td>_______</td>
                            <td>Sick hours accrued</td>
                            <td>_______</td>
                            <td>Vacation hours accrued</td>
                            <td>_______</td>
                        </tr>
                        <tr>
                            <td>Comp. hours taken</td>
                            <td>_______</td>
                            <td>Sick hours taken</td>
                            <td>_______</td>
                            <td>Vacation hours taken</td>
                            <td>_______</td>
                        </tr>
                        <tr>
                            <td>Comp. hours balance</td>
                            <td>_______</td>
                            <td>Sick hours balance</td>
                            <td>_______</td>
                            <td>Vacation hours balance</td>
                            <td>_______</td>
                        </tr>
                        <tr className="overtimeRow">
                            <td />
                            <td />
                            <td />
                            <td />
                            <td>Overtime hours</td>
                            <td>_______</td>
                        </tr>
                    </table>

                    <br />

                    <table className="signatures">
                        <tbody>
                            <tr>
                                <td>
                                    <h3>Officer's Signature</h3>
                                </td>
                                <td>_______________________________</td>
                            </tr>
                            <tr>
                                <td>
                                    <h3>Supvervisor's Signature</h3>
                                </td>
                                <td>_______________________________</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </React.Fragment>
        );
    }
}

ReactDom.render(
    <MuiThemeProvider>
        <App />
    </MuiThemeProvider>,
    document.getElementById('root')
);



